import React, {useEffect, useState, useRef} from 'react';
import { Helmet } from "react-helmet";
import { Dropdown } from 'react-bootstrap'
import { NavLink } from "react-router-dom";
import { ReactComponent as FiltersIcon } from '../../../assets/images/filters-icon.svg';
import {useHistory, useLocation, useParams} from "react-router-dom"
import FilterByDate from '../../Shared/FilterByDate/FilterByDate'
import FilterByDirection from '../../Shared/FilterByDirection/FilterByDirection'
import EventsListItem from "./components/EventsListItem"
import cn from "classnames";
import map from "lodash/map"
import eventService from '../../../services/events.service';

const EventList = () => {

    const [eventsList, setEventsList] = useState([]);
    const [isShowMore, setIsShowMore] = useState(true);
    const location = useLocation()
    const history = useHistory()
    let slug  = useParams();
    const [storeSlugFilter, setStoreSlugFilter] = useState(0)

    const handleShowMore = () => {
        if (slug.filter){
            eventService.getFilteredList(slug.filter, eventsList.length + location.search)
            .then(response => {
                if(response.data.length) {
                    setEventsList([...eventsList, ...response.data])
                } 
                else {
                    setIsShowMore(false)
                }
            });
        }
        else {
            eventService.getAllEvents(`${eventsList.length}${location.search}`)
            .then(response => {
                if(response.data.length) {
                    setEventsList([...eventsList, ...response.data])
                } 
                else {
                    setIsShowMore(false)
                }
            });
        }
    };
    
    const getData = (params) => {
        if (slug.filter) {
            eventService.getFilteredList(slug.filter, params)
                    .then(response => {
                        setEventsList(response.data)
                    });
        }
        else {
            eventService.getAllEvents(params)
            .then(response => {
                setEventsList(response.data)
            });
        }
    };

    const resetFilterParams = () => {
        getData(`0`)
        history.push({
            pathname: location.pathname,
            search: ``
        });
    }

    const mounted = useRef();
    
    useEffect(() => {
        if (!mounted.current) {
            setStoreSlugFilter(slug.filter)
            mounted.current = true;
            getData(`0${location.search}`)
        }
        else if (storeSlugFilter !== slug.filter){
            setStoreSlugFilter(slug.filter)
            getData(`0`)
        } 
    });
    return (
        <>
            <Helmet>
                <title>События</title>
            </Helmet>
            <div className="events-list-page">
                <div className="container">
                    <div className="d-flex align-items-center justify-content-between">
                        <h1 className='main-title'>События</h1>

                        <button className='mobile-filter' onClick={() => {}}>
                            <FiltersIcon />
                        </button>
                        <NavLink
                            to={'/events/active'}
                            className={cn({'active': slug.filter === 'active'}, 'navFilter')}
                        >
                            <span>актуальные</span>
                        </NavLink>
                        <NavLink
                            to={'/events/completed'}
                            className={cn({'active': slug.filter === 'completed'}, 'navFilter')}
                        >
                            <span>реализованные</span>
                        </NavLink>
                        <Dropdown className="filter-dropdown">
                            <Dropdown.Toggle id='eventsFilter'>
                                <span>Применить фильтр</span>
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                <FilterByDate buttonClass={'dropdown-item'} onGetData={getData}/>
                                <FilterByDirection buttonClass={'dropdown-item'} onGetData={getData}/>
                                {location.search && <NavLink as="button" onClick={resetFilterParams} className={'dropdown-item'} activeClassName={'events-active'} to={'/events'}>Сбросить</NavLink>}
                            </Dropdown.Menu>
                        </Dropdown>
                    </div>

                    <div className="row events-list">
                        {map(eventsList, (item, key) => {
                            return <div className='col-xl-4 col-md-6 col-12 mb-md-3' key={item.id}>
                                <EventsListItem {...item} />
                            </div>
                            })
                        }
                    </div>

                    {isShowMore && <button className='show-more' onClick={() => handleShowMore()}>Показать еще</button>}
                </div>
            </div>
        </>
    )
}
export default EventList