import React, {useState, useEffect, useRef} from 'react';
import { Helmet } from "react-helmet";
import cn from 'classnames';
import { useParams, NavLink } from "react-router-dom";
import map from "lodash/map"
import directionService from '../../../../services/direction.service'
import Documents from "./components/Documents"
const About = (props) => {
    const [trendData, setTrendData] = useState({})
    const [contentReadMore, setContentReadMore] = useState(false)
    const [docsReadMore, setDocsReadMore] = useState(false)

    let slug  = useParams();
    const [storeSlugId, setStoreSlugId] = useState(0)
    
    const mounted = useRef();
    
    useEffect(() => {
        if (!mounted.current) {
            setStoreSlugId(slug.id)
            mounted.current = true;
            directionService.getDirectionAbout(slug.id)
            .then(response => {
                setTrendData(response.data)
            })
        }
        else if (storeSlugId !== slug.id){
            setStoreSlugId(slug.id)
            directionService.getDirectionAbout(slug.id)
                .then(response => {
                    setTrendData(response.data)
                })
        } 
    }, [storeSlugId, slug.id]);


    const handleContentReadMore = () => {
        setContentReadMore(!contentReadMore)
    };

    const handleDocsReadMore = () => {
        setDocsReadMore(!docsReadMore)
    };
    const renderDocuments = () => {
        if(docsReadMore) {
            return trendData.documents;
        } else {
            return trendData.documents.slice(0, 4);
        }
    };
    
    return (
        <React.Fragment>
            <Helmet>
                <title>{`${props.title} - О направлении`}</title>
            </Helmet>

            {trendData.text &&
                <div className="direction-page__block">
                    {
                        !contentReadMore &&
                        trendData.text.length > 300 ?
                        <div className={cn('direction-page__content')} dangerouslySetInnerHTML={{ __html: trendData.text.slice(0, 300) + '...' }} /> :
                        <div className={cn('direction-page__content')} dangerouslySetInnerHTML={{ __html: trendData.text }} />
                    }

                    {
                        trendData.text.length > 300 &&
                        (<button className={cn('direction-page__show-content', { 'direction-page__show-content--open': contentReadMore })}
                                onClick={() => handleContentReadMore()}>
                            {contentReadMore ? 'Свернуть' : 'Читать далее'}
                        </button>)
                    }
                </div>
            }
            {trendData.directions && trendData.directions.length > 0 &&
                <div className="direction-page__block">
                    <h3 className="direction-page__title">Тэги направления</h3>

                    <div className="direction-links">
                        {map(trendData.directions, (item,key) => {
                        return <NavLink to={`/search?q=${item.title}`} className="direction-links__item"><span>{item.title}</span></NavLink> 
                        })}
                    </div>
                </div>
            }
            {trendData.documents && trendData.documents.length > 0 &&
                <div className="direction-page__block">
                    <h3 className="direction-page__title">Документы</h3>

                    <div className="doc-list">
                        {map(renderDocuments(), (item, key) => {
                            return <Documents key={item.id} {...item} />
                        })}
                    </div>
                    {
                        trendData.documents.length > 4 &&
                        (<button
                            className={cn('direction-page__show-content', { 'direction-page__show-content--open': contentReadMore })}
                            onClick={() => handleDocsReadMore()}
                        >
                            {docsReadMore ? 'Свернуть' : 'Показать все'}
                        </button>)
                    }
                </div>
            }
        </React.Fragment>
    )
}
export default About
