import React, {useState, useRef} from 'react';
import { Redirect, withRouter, useLocation } from "react-router-dom";

import { Modal } from 'react-bootstrap';
import searchPic from './images/search.svg'

const SearchBlock = () => {
    const [isShow, setIsShow] = useState(false)
    const [redirectPath, setRedirectPath] = useState(null)
    const input = useRef()
    const location = useLocation()

    const onToggleModal = (state) => {
        setIsShow(state)
    };

    const handleSubmitForm = (e) => {
        e.preventDefault();

        const page = location.pathname.split('/');
        setIsShow(false)
        setRedirectPath(`/search?q=${input.current.value}&p=${page[1]}`)
    };

    const renderRedirect = () => {
        if (redirectPath) {
            return <Redirect to={redirectPath} />
        }
    };
    return (
        <React.Fragment>
            {renderRedirect()}
            <Modal
                show={isShow}
                onHide={() => onToggleModal(false)}
                dialogClassName="common-modal-dialog"
            >
                <Modal.Header closeButton>
                    <Modal.Title>Поиск по сайту</Modal.Title>
                </Modal.Header>

                <Modal.Body>
                    <form onSubmit={handleSubmitForm}>
                        <input type="search" ref={input} placeholder="Вводимый текст..." />
                    </form>
                </Modal.Body>
            </Modal>
            <button className="social-block__search" onClick={() => onToggleModal(true)}>
                <img className="social-block__search-img" width="18" height="18" src={searchPic} alt=""/>
            </button>

        </React.Fragment>
    )

} 
export default withRouter(SearchBlock)