import axios from 'axios'

export default {
    getMainDirection() {
        return axios.get(`${process.env.REACT_APP_API_BASE_URL}/api/common/trends/`, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': "*"
            }
        })
    },
    getDirectionAbout(id) {
        return axios.get(`${process.env.REACT_APP_API_BASE_URL}/api/trend/${id}`, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': "*"
            }
        })
    }
}