import React, {useEffect, useState, useRef} from 'react';
import { Helmet } from "react-helmet";
import { Dropdown } from 'react-bootstrap'
import { NavLink } from "react-router-dom";
import { ReactComponent as FiltersIcon } from '../../../assets/images/filters-icon.svg';
import FilterByDate from '../../Shared/FilterByDate/FilterByDate'
import FilterByDirection from '../../Shared/FilterByDirection/FilterByDirection'
import NewsListItem from './components/NewsListItem'
import {useHistory, useLocation} from "react-router-dom"

import newsService from '../../../services/news.service';

const NewsList = (props) => {
    const [newsList, setNewsList] = useState([]);
    const [isShowMore, setIsShowMore] = useState(true);
    const location = useLocation()
    const history = useHistory()

    const handleShowMore = () => {
        newsService.getAllNews(`${newsList.length}${location.search}`)
            .then(response => {
                if(response.data.length) {
                    setNewsList([...newsList, ...response.data])
                } 
                else {
                    setIsShowMore(false)
                }
            });
    };

    const getData = (params) => {
        newsService.getAllNews(params)
            .then(response => {
                setNewsList(response.data)
            });
    };

    const resetFilterParams = () => {
        getData(`0`)
        history.push({
            pathname: location.pathname,
            search: ``
        });
    }

    const mounted = useRef();
    
    useEffect(() => {
        if (!mounted.current) {
            mounted.current = true;
            getData(`0${location.search}`)
        } 
    });
    
    
    
    return (
        <React.Fragment>
            <Helmet>
                <title>Новости</title>
            </Helmet>
            <div className="news-list-page">
                <div className="container">
                    <div className="d-flex align-items-center justify-content-between">
                <h1 className='main-title'>Новости</h1>

                        <button className='mobile-filter' onClick={() => {}}>
                            <FiltersIcon />
                        </button>

                        <Dropdown className="filter-dropdown">
                            <Dropdown.Toggle id='newsFilter'>
                                <span>Применить фильтр</span>
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                <FilterByDate buttonClass={'dropdown-item'} onGetData={getData}/>
                                <FilterByDirection buttonClass={'dropdown-item'} onGetData={getData}/>
                                {location.search && <NavLink as="button" onClick={resetFilterParams} className={'dropdown-item'} activeClassName={'news-active'} to={'/news'}>Сбросить</NavLink>}
                            </Dropdown.Menu>
                        </Dropdown>
                    </div>

                    <div className="news-list">
                        {newsList.map(item => (
                            <NewsListItem {...item} key={item.id} />
                        ))}
                    </div>

                    {isShowMore && <button className='show-more' onClick={() => handleShowMore()}>Показать еще</button>}
                </div>
            </div>
        </React.Fragment>
    );
    
}
export default NewsList