import { NavLink } from "react-router-dom";
import React, {useState} from 'react';
import styles from "./Navigate.module.scss";
import map from "lodash/map"

const Navigate = (props) => {
    const [navigates, setNavigates] = useState([
        {
            'code': 'about',
            'title': 'О направлении'
        },
        {
            'code': 'news',
            'title': 'Новости'
        },
        {
            'code': 'events',
            'title': 'События',
        },
    ]);

    return (
        <div className={styles.block}>
            {map(navigates, (item, key) => {
                return <div className={styles.item} key={item.code}>
                            <NavLink to={`/direction/${props.id}/${item.code}`} activeClassName={styles.active}>
                                <span>{item.title}</span>
                            </NavLink>

                            {item.children &&
                                <div className={styles.dropdown}>
                                    {map(item.children, (dItem, key) => {
                                        return <NavLink to={`/direction/${props.id}/${dItem.code}`} key={dItem.code}>
                                                    <span>{dItem.title}</span>
                                                </NavLink>
                                    })}
                                </div>
                            }
                        </div>
            })}
        </div>
    );
}
export default Navigate