import React from 'react';
import { NavLink } from "react-router-dom";
import helper from '../../../../helpers'
import newsMore from "../../../../assets/images/news-more.svg";
import eventsBlockTime from '../../../../assets/images/opportunity-block__time.svg';
import eventsBlockAddress from '../../../../assets/images/opportunity-block__address.svg';

const EventsListItem = (props) => {
    
    return (
        <NavLink to={`/events/${props.id}`} className="events-list__item">
                <div className="events-list__image">
                <img src={process.env.REACT_APP_API_BASE_URL + props.image} alt=""/>
            </div>

            <div className="events-list__title">{props.title}</div>

            <div className="events-list__text">
                {
                    props.time && (
                        <div className="events-list__text-item">
                            <div className="events-list__text-icon">
                                <img src={eventsBlockTime} alt=""/>
                            </div>
                            <span>{props.time}</span>
                        </div>
                    )
                }

                {
                    props.address && (
                        <div className="events-list__text-item">
                            <div className="events-list__text-icon">
                                <img src={eventsBlockAddress} alt=""/>
                            </div>
                            <span>{props.address}</span>
                        </div>
                    )
                }
            </div>

            {
                props.date && (
                    <div className="common-date-block events-list__date">
                        {props.date}
                    </div>
                )
            }
            </NavLink>
    )
}
export default EventsListItem