import React from 'react';
import { Switch, Route } from 'react-router-dom'
import '../../../assets/styles/main.css'
import '../../../assets/styles/main.scss'

import commonService from '../../../services/common.services'

import Header from '../Header/Header'
import Footer from '../Footer/Footer'

import Home from '../../Pages/Home/Home'
import NewsList from '../../Pages/NewsList/NewsList'
import NewsDetail from '../../Pages/NewsDetail/NewsDetail';
import EventList from '../../Pages/EventList/EventList'
import Trend from '../../Pages/Trend/Trend'
import EventDetail from '../../Pages/EventDetail/EventDetail'
import Federation from "../../Pages/About/About"
import Search from "../../Pages/Search/Search"
export default class Main extends React.Component{
    
    constructor(props) {
        super(props);

        this.state = {
            data: {}
        }
    }

    componentDidMount() {
        commonService.getCommonData()
            .then(response => {
                // console.log(response)
                this.setState({ data: response.data })
                
            })
            .catch(error =>{
                alert(error)
            })
    }

    render(){
        //console.log(this.state.data && this.state.data.main);
        return (
            <div className="wrapper dis-flex">
                <Header {...this.state}/>
                <div className="main-content pos-relative">
                    <Switch>
                        <Route exact path="/" render={(props) => <Home {...props} banner={this.state.data.main && this.state.data.main.banner}/>} />
                        <Route path="/federation" component={Federation} />
                        <Route exact path="/news/" component={NewsList} />
                        <Route path="/news/:id" component={NewsDetail} />
                        <Route exact path="/events" component={EventList} />
                        <Route exact path={"/events/:filter(active|completed)"} component={EventList} />
                        <Route path="/events/:id" component={EventDetail} />
                        <Route path={["/direction/:id", "/direction"]} component={Trend} />

                        <Route path={"/search"} component={Search} />

                    </Switch>
                </div>    
                <Footer {...this.state}/>
            </div>
            
        );
    }
}