import React, {useState, useEffect, useRef} from 'react';
import { useParams } from "react-router-dom";
import eventService from '../../../services/events.service';

import addressIcon from '../../../assets/images/address-icon.svg'
import calendarIcon from '../../../assets/images/calendar-icon.svg'
import timeIcon from '../../../assets/images/time-icon.svg'

import { Helmet } from "react-helmet";
import { NavLink } from "react-router-dom";
import map from "lodash/map"
import {
    VKShareButton,
    VKIcon,
    FacebookShareButton,
    FacebookIcon,
    TwitterShareButton,
    TwitterIcon,
    OKShareButton,
    OKIcon,
} from 'react-share';

const EventDetail = () => {
    const [event, setEvent] = useState(null)
    const [similarEvents, setSimilarEvents] = useState([])
    let slug  = useParams();
    const [storeSlugId, setStoreSlugId] = useState(0)
    const getData = () => {
        eventService.getEventDetail(slug.id)
            .then(response => {
                setEvent(response.data.event)
                setSimilarEvents(response.data.similarEvents)
            })
    };

    

    const mounted = useRef();
    
    useEffect(() => {
        if (!mounted.current) {
            mounted.current = true;
            getData()
            setStoreSlugId(slug.id)
        }
        else if (storeSlugId !== slug.id){
            getData()
            setStoreSlugId(slug.id)
        } 
    });

    return (
        <>
            <Helmet>
                <title>{event && event.title}</title>
                <meta property="og:title" content={event && event.title} />
                {event &&<meta property="og:image" content={process.env.REACT_APP_API_BASE_URL + event.image} />}
            </Helmet>
            {event && 
                <div className="event-detail-page">
                    <div className="container">
                        <div className="row">
                            <div className="col">
                                <div className="event-detail">
                                    <h2>{event.title}</h2>
                                    <div className="event-detail__main-image">
                                        <img src={ process.env.REACT_APP_API_BASE_URL + event.image } alt=""/>

                                        <div className="event-info">

                                            {event.date &&
                                                <div className="event-info__item">
                                                    <div className="event-info__icon">
                                                        <img src={calendarIcon} alt=""/>
                                                    </div>
                                                    <span>{event.date}</span>
                                                </div>
                                            }

                                            {event.time &&
                                                <div className="event-info__item">
                                                    <div className="event-info__icon">
                                                        <img src={timeIcon} alt=""/>
                                                    </div>
                                                    <span>{event.time}</span>
                                                </div>
                                            }

                                            {event.address &&
                                                <div className="event-info__item">
                                                    <div className="event-info__icon">
                                                        <img src={addressIcon} alt=""/>
                                                    </div>
                                                    <a href={event.map} target='_blank' rel="noopener noreferrer">{event.address}</a>
                                                </div>
                                            }
                                        </div>
                                    </div>

                                    <div className="event-detail__content">
                                            {event.text &&
                                            <div className="event-detail-block__text">
                                                <div dangerouslySetInnerHTML={{ __html: event.text }} />
                                            </div>
                                            }                                        
                                    </div>

                                    <div className="event-detail__tags">
                                        <VKShareButton className="mr-2" url={window.location.href}>
                                            <VKIcon size={30} round={true}/>
                                        </VKShareButton>

                                        <FacebookShareButton className="mr-2" url={window.location.href}>
                                            <FacebookIcon size={30} round={true}/>
                                        </FacebookShareButton>

                                        <TwitterShareButton className="mr-2" url={window.location.href}>
                                            <TwitterIcon size={30} round={true}/>
                                        </TwitterShareButton>

                                        <OKShareButton className="mr-2" url={window.location.href}>
                                            <OKIcon size={30} round={true}/>
                                        </OKShareButton>
                                    </div>

                                    <div className="event-detail__tags">
                                        {map(event.directions, (item, key) => {
                                            return <NavLink key={item.id} to={`/search?q=${item.title}&p=events`}>{item.title}</NavLink>
                                            })
                                        }
    
                                    </div>
                                </div>
                            </div>

                            <div className="col col-sidebar">
                                <div className="position-sticky">
                                    <div className="row">
                                        
                                        {similarEvents.length > 0 &&
                                        <div className="col-12">
                                            <div className="sidebar-event">
                                                <div className="sidebar-title">похожие события</div>

                                                <div className="row">
                                                    {map(similarEvents, (item,key) => {
                                                        return <div className="col-12" key={item.id}>
                                                            <NavLink className="sidebar-event-block" to={`/events/${item.id}`}>{item.title}</NavLink>
                                                        </div>
                                                        })
                                                    }
                                                </div>
                                            </div>
                                        </div>
                                        }
                                    </div>
                                    <div>
                                        <NavLink className={'sidebar-more'} to={`/events`}>Все события</NavLink>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            }
        </>
    )
}
export default EventDetail