import React, {useState,useEffect} from 'react';
import { Helmet } from "react-helmet";
import eventsService from '../../../../services/events.service';
import EventItem from '../../../Shared/EventItem/EventItem';
import {useParams} from "react-router-dom"
import map from "lodash/map"
import EventList from '../../EventList/EventList';
const EventsTab = (props) => {
    const [events, setEvents] = useState([])
    const [isShowMore, setIsShowMore] = useState(true)
    
    let slug  = useParams();

    useEffect(() => {
        eventsService.getDirectionEvents(slug.id, 0)
            .then(response => {
                if (!response.data.length) {
                    setIsShowMore(false)
                }
                setEvents(response.data)
            })
         
    }, []);

    
    const handleShowMore = () => {
        eventsService.getDirectionEvents(slug.id, events.length)
            .then(response => {
                if(response.data.length) {
                    setEvents([...events, ...response.data])
                } else {
                    setIsShowMore(false)
                }

            });
    };


    
    return (
        <React.Fragment>
            <Helmet>
                <title>Возможности - {props.title}</title>
            </Helmet>

            <div className="direction-page__block">
                <div className="row mb-4">
                    {map(events, (item, key) => {
                        return <div className="col-sm-6 col-12 mb-3" key={item.id}>
                                    <EventItem event={item} small direction />
                                </div>
                    })}
                </div>

                {isShowMore && <button className='show-more' onClick={() => handleShowMore()}>Показать еще</button>}
            </div>
        </React.Fragment>
    )

}
export default EventsTab

