import React, {useEffect, useState} from 'react';
import { Helmet } from "react-helmet";
import { ScreenWidth } from '../../../context';
import cn from 'classnames';
import map from "lodash/map"
import CommitteeItem from './components/CommitteeItem'

import aboutService from '../../../services/about.service';


import mailIcon from '../../../assets/images/mail-icon.svg';
import phoneIcon from '../../../assets/images/phone-icon.svg';
import addressIcon from '../../../assets/images/address-icon.svg';

const AboutPage = () => {
    const [aboutData, setAboutData] = useState({})
    useEffect(() => {
        aboutService.getAboutData()
        .then(response => {
            let data = response.data;
            data.federationelement_set = map(data.federationelement_set, (item, key) => {
                item.isOpen = false;
                return item;
            });
            console.log(data)
            console.log({...data})
            setAboutData({...data})
        })
    }, []);

    const sidebarRender = () => {
        return (
            <div className="col col-sidebar">
                <div className="sidebar-sticky">
                    <h3>Режим работы</h3>

                    {aboutData.work_time &&
                        <div className="sidebar-block" dangerouslySetInnerHTML={{ __html: aboutData.work_time }} />
                    }

                    <h3>Общая информация</h3>

                    <div className="sidebar-block">
                        <div className="contacts">
                            {aboutData.email &&
                                <div className="contacts__item">
                                    <div className="contacts__icon">
                                        <img src={mailIcon} alt=""/>
                                    </div>
                                    <a href={`mailto:${aboutData.email}`}>{aboutData.email}</a>
                                </div>
                            }
                            {aboutData.phone &&
                                <div className="contacts__item">
                                    <div className="contacts__icon">
                                        <img src={phoneIcon} alt=""/>
                                    </div>
                                    <a href={`tel:${aboutData.phone}`}>{aboutData.phone}</a>
                                </div>
                            }
                            {aboutData.address &&
                                <div className="contacts__item">
                                    <div className="contacts__icon">
                                        <img src={addressIcon} alt=""/>
                                    </div>
                                    <span>{aboutData.address}</span>
                                </div>
                            }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    const renderCommitteeItem = (blockItem) => {
        let data = blockItem.isOpen ?
            blockItem.federationstaff_set :
            blockItem.federationstaff_set.slice(0, 2);

        return map(data, (people, item) => {
            return <CommitteeItem {...people} key={people.id} />
        })
    };

    const toggleCommittee = (blockItem) => {
        const federationelement_set = map(aboutData.federationelement_set, (item, key) => {
            if(item === blockItem) {
                item.isOpen = !item.isOpen;
            }

            return item;
        });
        setAboutData(...aboutData, federationelement_set)
    };

    
    return (
        <ScreenWidth.Consumer>
            {({screenWidth}) => (
                <React.Fragment>
                    <Helmet>
                        <title>Федерация</title>
                    </Helmet>

                    <div className="about-page">
                        <div className="container">
                            <h1 className={'main-title'}>О нас</h1>

                            <div className="row">
                                {screenWidth < 1024 && sidebarRender()}

                                <div className="col">
                                    {aboutData.text && <div className='about-text' dangerouslySetInnerHTML={{ __html: aboutData.text }} />}

                                    {aboutData.federationelement_set && map(aboutData.federationelement_set, (blockItem, key) => {
                                        return <div className="committee" key={blockItem.id}>
                                                    <h3 className="committee__title">{blockItem.title}</h3>

                                                    <div className="committee-personal-list">
                                                        {renderCommitteeItem(blockItem)}
                                                    </div>

                                                    {
                                                        blockItem.federationstaff_set.length > 2 &&
                                                        <button className={cn('committee__more', { 'committee__more--open': blockItem.isOpen })} onClick={() => toggleCommittee(blockItem)}>
                                                            {blockItem.isOpen ? 'Свернуть' : 'Смотреть все'}

                                                        </button>
                                                    }
                                                </div>
                                    })}
                                </div>

                                {screenWidth >= 1024 && sidebarRender()}
                            </div>
                        </div>
                    </div>
                </React.Fragment>
            )}
        </ScreenWidth.Consumer>
    );

}
export default AboutPage
