import React, {useEffect, useState, useRef} from 'react';
import find from "lodash/find"
import {useParams, Redirect, Route, Switch} from "react-router-dom"
import Navigate from "./components/Navigate"
import directionService from '../../../services/direction.service';
import TrendList from "./components/TrendList"
import TrendAbout from "./About/About"
import TrendsNews from "./News/News"
import TrendsEvents from "./Events/Events"
const Trend = (props) => {
    const [trendsList, setTrendsList] = useState([]);
    let slug  = useParams();
    const [storeSlugId, setStoreSlugId] = useState(0)
    
    

    const getData = () => {
        directionService.getMainDirection()
            .then(response => {
                setTrendsList(response.data)
            });
    };

    
    const mounted = useRef();
    
    useEffect(() => {
        if (!mounted.current) {
            mounted.current = true;
            getData()
        }
        else if (storeSlugId !== slug.id){
            setStoreSlugId(slug.id)
            getData()
        } 
    }, [storeSlugId, slug.id]);
    
    
    
    return (
        <React.Fragment>
            <div className="direction-page">
                <div className="container">
                    {trendsList.length && slug.id &&
                        <React.Fragment>
                            <h1 className={'main-title'}>{find(trendsList, { id: +slug.id }).title}</h1>

                            <div className="row">
                                <div className="col">
                                    <Navigate id={slug.id} />
                                    <Switch>
                                        <Route
                                            path={"/direction/:id/about"}
                                            render={(props) => <TrendAbout title={find(trendsList, { id: +slug.id }).title} />} />
                                        <Route
                                            exact
                                            path={"/direction/:id/events"}
                                            render={(props) => <TrendsEvents title={find(trendsList, { id: +slug.id }).title} />} />
                                        <Route
                                            path={"/direction/:id/news"}
                                            render={(props) => <TrendsNews  title={find(trendsList, { id: +slug.id }).title} />}  />

                                        <Redirect to={`/direction/${slug.id}/about`}/>
                                    </Switch>
                                </div>
                                <div className="col col-sidebar">
                                    <TrendList list={trendsList} />
                                </div>
                            </div>
                        </React.Fragment>
                    }
                </div>
            </div>
        </React.Fragment>
    );
    
}
export default Trend