import React, {useState, useEffect} from "react";
import { Modal } from "react-bootstrap";
import {useHistory, useLocation} from "react-router-dom"
import PropTypes from 'prop-types'
import helper from "../../../helpers";
import directionService from '../../../services/direction.service';
import "./FilterByDirection.css"
const propTypes = {
    buttonClass: PropTypes.string,
    onGetData: PropTypes.func,
}
const defaultProps = {
    buttonClass: "",
    onGetData: () => {},
}

const FilterByDirection = (props) => {
    const [directionList, setDirectionList] = useState([]);
    const [isShow, setIsShow] = useState(false);
    const history = useHistory()
    const location = useLocation()

    const onToggleModal = (state) => {
        setIsShow(state)
    };

    const selectDirection = (id) => {
        onToggleModal(false);
        let strSearch = helper.joinLocationParamsByDirection(location.search, id)
        
        history.push({
            pathname: location.pathname,
            search: strSearch
        })
        let objSearch = helper.join2ObjectLocation(strSearch)
        props.onGetData(`0${helper.convertObject2Get(objSearch)}`, true);
    };

    useEffect(() => {
        directionService.getMainDirection()
            .then(response => {
                setDirectionList(response.data)
            })
    }, []);

    return (
        <>
        <button className={props.buttonClass} onClick={() => onToggleModal(true)}>По направлению</button>
        <Modal
            show={isShow}
            onHide={() => onToggleModal(false)}
            dialogClassName="common-modal-dialog"
        >
            <Modal.Header closeButton>
                <Modal.Title>Выберите направление</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="overflow-auto">
                    <div className="direction-list">
                        {directionList.map(item => (
                            <button key={item.id} 
                                    onClick={() => selectDirection(item.id)}>
                                {item.title}
                            </button>
                        ))}
                    </div>
                </div>
            </Modal.Body>      
        </Modal>       
        </>
    );
}
FilterByDirection.defaultProps = defaultProps
FilterByDirection.propTypes = propTypes 
export default FilterByDirection