import React, {useState, useEffect} from 'react';
import { Helmet } from "react-helmet";
import { Link, useLocation, useHistory } from "react-router-dom";
import cn from "classnames";

import searchService from '../../../services/search.service';
import map from "lodash/map"
import helpers from '../../../helpers';


import {ReactComponent as ButtonIcon} from "../../../assets/images/search-page-button.svg";


const Search = (props) => {
    const location = useLocation()
    const history = useHistory()

    const [query, setQuery] = useState(helpers.getSearchQuery(location.search, 'q'))
    const [prevPage, setPrevPage] = useState(helpers.getSearchQuery(location.search, 'p'))
    const [input, setInput] = useState(React.createRef())
    const [count, setCount] = useState(0)
    const [isShowMore, setIsShowMore] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [results, setResults] = useState([])
    const [categories, setCategories] = useState({news: 'Новость',events: 'Событие',direction: 'Направление'})

    useEffect(() => {
        if(query) {
            searchService.getSearchResult(0, `q=${input.current.value}&p=${prevPage}`)
                .then(response => {
                    setCount(response.data.count)
                    setResults(response.data.data)
                })
        }
    }, [input, prevPage, query]);


    const handleSubmitForm = (e) => {
        e.preventDefault();

        searchService.getSearchResult(0, `q=${input.current.value}&p=${prevPage}`)
            .then(response => {
                setQuery(input.current.value)
                setCount(response.data.count)
                setResults(response.data.data)
            });

        history.push({
            pathname: '/search',
            search: `q=${input.current.value}`
        });
    };

    const handleShowMore = () => {
        searchService.getSearchResult(results.length, `q=${query}&p=${prevPage}`)
            .then(response => {
                if(response.data.data.length) {
                    setResults([...results, ...response.data.data])
                } else {
                    setIsShowMore(false)
                }

            });
    };

    
    return (
        <React.Fragment>
            <Helmet>
                <title>Результаты поиска {query}</title>
            </Helmet>

            <div className="search-page">
                <div className="container">
                    <h1 className="main-title">Результаты поиска</h1>

                    <div className="search-form">
                        <form className={'d-flex'} onSubmit={handleSubmitForm}>
                            <input type="text" ref={input} defaultValue={query} placeholder="Текст поискового запроса" />
                            <button type={'submit'}>
                                <ButtonIcon />
                            </button>
                        </form>
                    </div>

                    <div className="search-title">
                        По запросу {query && <b>{query}</b>} найдено: <b>{count}</b>
                    </div>

                    <div className="search-result">
                        {map(results, (item, key) => {
                            {console.log(item)}
                            return <Link to={`/${item.type}/${item.id}`} className="search-result__item" key={item.id}>
                                        <div className="search-result__type">{categories[item.type]}</div>
                                        <div className="search-result__title">{item.title}</div>
                                        <p>{item.titlePreview}</p>
                                    </Link>
                        })}
                    </div>

                    {isShowMore && <button className={cn('show-more', 'mt-5', { 'isLoading': isLoading })} onClick={() => handleShowMore()}>Показать еще</button>}
                </div>
            </div>
        </React.Fragment>
    )

}
export default Search
