import moment from 'moment';
import 'moment/locale/ru';
import replace from "lodash/replace"
import split from "lodash/split"
import forEach from "lodash/forEach"

export default {
    getVideoId(url) {
        if(url) {
            const urlData = new URL(url);
            return urlData.searchParams.get('v');
        } else {
            return '';
        }

    },
    convert2DatePicker(utcDate) {
        if(utcDate) {
            return moment(utcDate).format('D.M.YYYY');
        } else {
            return null;
        }

    },
    convert2LL(utcDate) {
        return moment(utcDate).format('D MMMM YYYY');
    },
    convertObject2Get(obj) {
        return obj ? '?' + Object.keys(obj).map(key => `${key}=${obj[key]}`).join('&') : '';
    },
    joinLocationParamsByDirection(search, id){
        const regSearch = /direction=\d*/
        let strSearch = search.indexOf("direction") === -1 ? search.indexOf("from") === -1 ? `direction=${id}` : `${search}&direction=${id}` : replace(search, regSearch, `direction=${id}`);
        return replace(strSearch, "?", "")
    },
    joinLocationParamsByDate(search, from, to){
        const regSearch = /from=\w*.\w*.\w*&to=\w*.\w*.\w*/
        let strSearch = search.indexOf("from") === -1 ? search.indexOf("direction") === -1 ? `from=${from}&to=${to}` : `${search}&from=${from}&to=${to}` : replace(search, regSearch, `from=${from}&to=${to}`);
        return replace(strSearch, "?", "")
    },
    join2ObjectLocation(strSearch){
        let objSearch = {}
        forEach(split(strSearch, "&"), function (item) {
            let tempStrSearch = split(item, "=") 
            objSearch[tempStrSearch[0]] = tempStrSearch[1] 
        })
        return objSearch
    },
    getSearchQuery(url, param) {
        if(url) {
            const splitData = new URLSearchParams(url);
            return decodeURI(splitData.get(param));
        } else {
            return "";
        }
    },
}