import React, {useState} from "react";
import { Modal } from "react-bootstrap";
import {useHistory, useLocation} from "react-router-dom"
import PropTypes from 'prop-types'
import helper from "../../../helpers";
import DatePicker, { registerLocale } from  "react-datepicker";
import ru from 'date-fns/locale/ru';
import "react-datepicker/dist/react-datepicker.css";
// CSS Modules, react-datepicker-cssmodules.css
import 'react-datepicker/dist/react-datepicker-cssmodules.css';
import "./FilterByDate.css"
registerLocale('ru', ru)

const propTypes = {
    buttonClass: PropTypes.string,
    onGetData: PropTypes.func,
}
const defaultProps = {
    buttonClass: "",
    onGetData: () => {},
}

const FilterByDate = (props) => {
    const [startDate, setStartDate] = useState(new Date("2020/05/01"));
    const [endDate, setEndDate] = useState(new Date("2020/05/31"));
    const [isShow, setIsShow] = useState(false);
    const history = useHistory()
    const location = useLocation()

    const onToggleModal = (state) => {
        setIsShow(state)
    };

    const selectDate = (from, to) => {
        if(!to) to = from;

        let strSearch = helper.joinLocationParamsByDate(location.search, from, to)
        let objSearch = helper.join2ObjectLocation(strSearch)

        props.onGetData(`0${helper.convertObject2Get(objSearch)}`, true);
        
        history.push({
            pathname: location.pathname,
            search: strSearch
        });
        onToggleModal(false);
    };
    return (
        <>
        <button className={props.buttonClass} onClick={() => onToggleModal(true)}>По дате</button>
        <Modal
            show={isShow}
            onHide={() => onToggleModal(false)}
            dialogClassName="common-modal-dialog"
        >
            <Modal.Header closeButton>
                <Modal.Title>Выберите дату</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="date-range">
                    <DatePicker
                        locale="ru"
                        inline
                        selected={startDate}
                        onChange={date => setStartDate(date)}
                        selectsStart
                        startDate={startDate}
                        endDate={endDate}
                        calendarClassName="filtersport-Time"
                    />
                    <DatePicker
                        calendarClassName="filtersport-Time"
                        locale="ru"
                        inline
                        selected={endDate}
                        onChange={date => setEndDate(date)}
                        selectsEnd
                        startDate={startDate}
                        endDate={endDate}
                        minDate={startDate}
                    />
                </div>
                <button
                    className={'date-range__apply btnSportStyle'}
                    disabled={!startDate && !endDate}
                    onClick={() => selectDate(
                        helper.convert2DatePicker(startDate),
                        helper.convert2DatePicker(endDate)
                    )}>
                    Применить
                </button>
            </Modal.Body>
            
        </Modal>
        
        </>
    );
}
FilterByDate.defaultProps = defaultProps
FilterByDate.propTypes = propTypes 
export default FilterByDate