import React, {useState, useEffect, useRef} from 'react';
import { useParams } from "react-router-dom";
import newsService from '../../../services/news.service';
import helper from "../../../helpers";
import { Helmet } from "react-helmet";
import { NavLink } from "react-router-dom";
import map from "lodash/map"
import {
    VKShareButton,
    VKIcon,
    FacebookShareButton,
    FacebookIcon,
    TwitterShareButton,
    TwitterIcon,
    OKShareButton,
    OKIcon,
} from 'react-share';

const NewsDetail = () => {
    const [news, setNews] = useState(null)
    const [nextNews, setNextNews] = useState({})
    const [previousNews, setPreviousNews] = useState({})
    const [similarNews, setSimilarNews] = useState([])
    let slug  = useParams();
    const [storeSlugId, setStoreSlugId] = useState(0)
    const getData = () => {
        newsService.getNewsDetail(slug.id)
            .then(response => {
                setNews(response.data.news)
                setNextNews(response.data.nextNews)
                setPreviousNews(response.data.previousNews)
                setSimilarNews(response.data.similarNews)
            })
    };

    

    const mounted = useRef();
    
    useEffect(() => {
        if (!mounted.current) {
            setStoreSlugId(slug.id)
            mounted.current = true;
            getData()
        }
        else if (storeSlugId !== slug.id){
            setStoreSlugId(slug.id)
            getData()
        } 
    });

    return (
        <>
            <Helmet>
                <title>{news && news.title}</title>
                <meta property="og:title" content={news && news.title} />
                {news &&<meta property="og:image" content={process.env.REACT_APP_API_BASE_URL + news.image} />}
            </Helmet>
            {news && 
                <div className="news-detail-page">
                    <div className="container">
                        <div className="row">
                            <div className="col">
                                <div className="news-detail">
                                    <div className="news-detail__main-image">
                                        <img src={process.env.REACT_APP_API_BASE_URL + news.image} alt=""/>
                                    </div>

                                    <h2>{news.title}</h2>

                                    <div className="news-detail__date">{helper.convert2LL(news.date)}</div>

                                    <div className="news-detail__content">
                                            {news.text &&
                                            <div className="news-detail-block__text">
                                                <div dangerouslySetInnerHTML={{ __html: news.text }} />
                                            </div>
                                            }                                        
                                    </div>

                                    <div className="news-detail__tags">
                                        <VKShareButton className="mr-2" url={window.location.href}>
                                            <VKIcon size={30} round={true}/>
                                        </VKShareButton>

                                        <FacebookShareButton className="mr-2" url={window.location.href}>
                                            <FacebookIcon size={30} round={true}/>
                                        </FacebookShareButton>

                                        <TwitterShareButton className="mr-2" url={window.location.href}>
                                            <TwitterIcon size={30} round={true}/>
                                        </TwitterShareButton>

                                        <OKShareButton className="mr-2" url={window.location.href}>
                                            <OKIcon size={30} round={true}/>
                                        </OKShareButton>
                                    </div>

                                    <div className="news-detail__tags">
                                        {map(news.directions, (item, key) => {
                                            return <NavLink key={item.id} to={`/search?q=${item.title}&p=news`}>{item.title}</NavLink>
                                            })
                                        }
    
                                    </div>
                                </div>
                            </div>

                            <div className="col col-sidebar">
                                <div className="position-sticky">
                                    <div className="row">
                                        {nextNews &&
                                        <div className="col-lg-12 col-sm-6">
                                            <div className="sidebar-news">
                                                <div className="sidebar-title">следующая новость</div>
                                                <NavLink className="sidebar-news-block" to={`/news/${nextNews.id}`}>{nextNews.title}</NavLink>
                                            </div>
                                        </div>
                                        }

                                        {previousNews &&
                                        <div className="col-lg-12 col-sm-6">
                                            <div className="sidebar-news">
                                                <div className="sidebar-title">предыдущая новость</div>
                                                <NavLink className="sidebar-news-block" to={`/news/${previousNews.id}`}>{previousNews.title}</NavLink>
                                            </div>
                                        </div>
                                        }

                                        {similarNews.length > 0 &&
                                        <div className="col-12">
                                            <div className="sidebar-news">
                                                <div className="sidebar-title">похожие новости</div>

                                                <div className="row">
                                                    {map(similarNews, (item,key) => {
                                                        return <div className="col-12" key={item.id}>
                                                            <NavLink className="sidebar-news-block" to={`/news/${item.id}`}>{item.title}</NavLink>
                                                        </div>
                                                        })
                                                    }
                                                </div>
                                            </div>
                                        </div>
                                        }
                                    </div>
                                    <div>
                                        <NavLink className={'sidebar-more'} to={`/news`}>Все новости</NavLink>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            }
        </>
    )
}
export default NewsDetail