import React, {useState, useEffect} from 'react';
import { NavLink, withRouter } from "react-router-dom";
import map from "lodash/map"
import styles from './TrendList.module.scss'



const TrendList = (props) => {
    const [stickyOffset, setStickyOffset] = useState(0)
    
    useEffect(() => {
        const windowHeight = document.documentElement.clientHeight;
        const intElemOffsetHeight = document.getElementById('stickySidebar').offsetHeight;
        setStickyOffset(windowHeight - intElemOffsetHeight)
    }, []);
    

    
    return (
        <div id="stickySidebar" className={styles.block} style={{ top: stickyOffset }}>
            {map(props.list, (item, key) => {
                return <NavLink
                            to={`/direction/${item.id}/about`}
                            activeClassName={styles.active}
                            className={styles.item} key={item.id}
                        >
                            <div className={styles.icon}>
                                <img src={process.env.REACT_APP_API_BASE_URL + item.imageInactive} alt=""/>
                                <img src={process.env.REACT_APP_API_BASE_URL + item.imageActive} alt=""/>
                            </div>

                            <span>{item.title}</span>
                        </NavLink>
            })}
        </div>
    )
}

export default withRouter(TrendList);
