import axios from 'axios'

export default {
    getMainEvents() {
        return axios.get(`${process.env.REACT_APP_API_BASE_URL}/api/common/events/`, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': "*"
            }
        })
    },
    getAllEvents(params = '') {
        return axios.get(`${process.env.REACT_APP_API_BASE_URL}/api/events/all/list/${params}`, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': "*"
            }
        })
    },
    getFilteredList(filter, params = '') {
        return axios.get(`${process.env.REACT_APP_API_BASE_URL}/api/events/${filter}/list/${params}`, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': "*"
            }
        })
    },
    getEventDetail(id) {
        return axios.get(`${process.env.REACT_APP_API_BASE_URL}/api/events/detail/${id}`, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': "*"
            }
        })
    },
    getDirectionEvents(id, offset) {
        return axios.get(`${process.env.REACT_APP_API_BASE_URL}/api/trend/directions/${id}/events/${offset}`, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': "*"
            }
        })
    },
}