import React, {useState, useEffect} from 'react';
import { Helmet } from "react-helmet";
import {useParams} from "react-router-dom"
import map from "lodash/map"
import newsService from '../../../../services/news.service'
import NewsItem from "../../../Shared/NewsItem/NewsItem";

const NewsTab = (props) => {
    const [news, setNews] = useState([])
    const [isShowMore, setIsShowMore] = useState(true)
    
    let slug  = useParams();

    useEffect(() => {
        newsService.getDirectionNews(slug.id, 0)
            .then(response => {
                if (!response.data.length) {
                    setIsShowMore(false)
                }
                setNews(response.data)
            })
         
    }, []);


    const handleShowMore = () => {
        newsService.getDirectionNews(slug.id, news.length)
            .then(response => {
                if(response.data.length) {
                    setNews([...news, ...response.data])
                } else {
                    setIsShowMore(false)
                }

            });
    };

    
    return (
        <React.Fragment>
            <Helmet>
                <title>Новости - {props.title}</title>
            </Helmet>

            <div className="direction-page__block">
                <div className="row mb-4">
                    {map(news, (item, key) => {
                        return <div className="col-sm-6 col-12 mb-3" key={item.id}>
                                    <NewsItem news={item} small direction />
                                </div>
                    })}
                </div>

                {isShowMore && <button className='show-more' onClick={() => handleShowMore()}>Показать еще</button>}
            </div>
        </React.Fragment>
    )

}
export default NewsTab
